@import url(https://fonts.googleapis.com/css2?family=PT+Serif:ital@1&family=Poppins:wght@200;300;700&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1, h4 {
  font-family: 'PT Serif', 'Times New Roman', serif;
  font-style: italic;
  font-weight: 400;
  color: #BC4C2A;
}

h2 {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 4px;
}

img {
  width: 100%;
}

main {
    margin: 24px 0;
    display: flex;
    justify-content: center;
}

nav {
    margin: 24px 0;
}

footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

section {
    background-color: #F6F3EF;
    font-size: 10px;
    height: 400px;
    overflow: hidden;
    flex-basis: 350px;
    flex-grow: 1;
    flex-shrink: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

section p {
    max-width: 200px;
}

button {
    border: 1px solid #BC4C2A;
    color:  #BC4C2A;
    background-color: transparent;
    font-size: 10px;
    padding: 8px 16px;
    text-transform: lowercase;
    cursor: pointer;
}

button:hover {
    background-color: #BC4C2A;
    color: white;
    transition: all .4s ease-in-out;
}

button:disabled {
    cursor: not-allowed;
    border-color: #D08169;
    color: #D08169;
}

button + button {
    margin-left: 12px;
}

article {
    width: 100px;
    position: relative;
    text-align: center;
    margin: 16px;
}

article span {
    background-color: #BC4C2A;
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    font-size: 8px;
    padding: 4px;
}

article p {
    font-size: 10px;
}
